<template><section><html><head></head><body><h1>CustomDataSource</h1>
<p>The <code v-pre="">vc-datasource-custom</code> component is used to load a dataSource which can be used to manually manage a group of entities.</p>
<h2>Example</h2>
<h3>Load a CustomDataSource with <code v-pre="">vc-datasource-custom</code></h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-datasource-custom ref="datasource" name="custom" :entities="entities" @click="clicked">
          <vc-entity @click="clicked" ref="entity1" :position="position" :billboard="billboard" :description="description" :id="id"> </vc-entity>
          <vc-entity ref="enttiy2" :position="position1" :description="description" :cylinder.sync="cylinder1">
            <vc-graphics-cylinder ref="cylinder1" :length="400000.0" :top-radius="200000.0" :bottom-radius="200000.0" :material="material1" :outline="true" :outline-color="outlineColor1"></vc-graphics-cylinder>
          </vc-entity>
          <vc-entity :position="position2" :description="description" :cylinder.sync="cylinder2">
            <vc-graphics-cylinder ref="cylinder2" :length="400000.0" :top-radius="0.0" :bottom-radius="200000.0" :material="material2"></vc-graphics-cylinder>
          </vc-entity>
        </vc-datasource-custom>
        <template v-for="(itemOut, indexOut) of datas">
          <vc-datasource-custom :key="indexOut" :name="'datasource' + indexOut + '_' + itemOut.color" :ref="'ds' + itemOut.name" @ready="datasourceReady(itemOut)" @clusterevent="datasourceClusterEvent" v-if="itemOut.type == 'point'" @click="clicked">
            <template v-for="(item, index) of itemOut.data">
              <vc-entity :key="index" :position="getPosition(item)" :ref="'entity' + index">
                <vc-graphics-billboard :image="itemOut.iconUrl" :scale="0.5" :show="true" :width="itemOut.width" :height="itemOut.height"></vc-graphics-billboard>
              </vc-entity>
            </template>
          </vc-datasource-custom>
        </template>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
